<template>
  <div class="report-generate">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">温度预测</div>
        </div>
      </div>
      <div class="common-flex">
        <div style="margin-right:10px">刷新频率</div>
        <div class="select" style="width:100px">
          <Select v-model="time" @on-change="freshTimeChange">
            <Option
              v-for="item in freshTime"
              :key="item.id"
              :value="item.value"
            >
              {{ item.name }}
            </Option>
          </Select>
        </div>
      </div>
      <div class="common-flex">
        <div class="right-boxes">
          <div
            class="box"
            v-for="(item, index) in tempList"
            :key="index"
            @click="changeChecked(item)"
          >
            <div
              class="nameline"
              :style="{
                background: item.id === checkedId ? '#0084ff' : '#141417'
              }"
            >
              {{
                item.id < 7
                  ? getEng(item.id) + "堆空气出口温度"
                  : "电堆空气出口温度"
              }}
            </div>
            <div
              class="value"
              :style="{
                border: item.id === checkedId ? '1px solid #0084ff' : 'none'
              }"
            >
              预测值：{{ Number(item.predTemperature).toFixed(2) + "℃" }}
            </div>
            <div
              class="value"
              :style="{
                border: item.id === checkedId ? '1px solid #0084ff' : 'none'
              }"
            >
              实际值：{{ Number(item.realTemperature).toFixed(2) + "℃" }}
            </div>
          </div>
        </div>
        <div style="margin-left: 30px;text-align: center">
          <div class="judge">是否判定故障</div>
          <div class="common-flex" style="justify-content: center">
            <Button @click="showModal(1)">是</Button>
          </div>
        </div>
      </div>
      <div class="link">
        <div class="title">{{ options.title }}</div>
        <div class="chart-container">
          <custom-chart
            :data="options.value"
            v-bind="options"
            :id="1050546547547"
            :height="520"
            ref="chart"
          ></custom-chart>
        </div>
      </div>
    </div>
      <Modal
        v-model="modal"
        title="提示"
        @on-ok="changeState(1)"
        @on-cancel="changeState(0)">
        <div style="font-size:16px;margin-bottom:12px">确认修改故障判定为{{modalType?'故障':'非故障'}}吗？</div>
        <Input placeholder="若确认,请输入故障代码" @input="getInput" v-if="modalType"></Input>
      </Modal>
  </div>
</template>

<script>
import StatusSelect from '@/components/status-select'
import LaboratoryList from '@/components/laboratory-list'
import OrgSelect from '@c/all-area-select'
import { getTemperatureInfo, changeTemperatureState } from '@/api/device'
import customChart from '@/components/common-chart/common-chart'
import { getTempLineTranslate } from '@/api/suanpan'
export default {
  name: 'report-list',
  components: {
    StatusSelect,
    LaboratoryList,
    OrgSelect,
    customChart
  },
  i18n_prefix: 'page.report-manage.report-generate',
  data () {
    return {
      time: 5000,
      freshTime: [
        { id: '1', name: '1s', value: 1000 },
        { id: '2', name: '5s', value: 5000 },
        { id: '3', name: '10s', value: 10000 }
      ],
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      title: '',
      currentIndex: 0,
      suanpanLink: '',
      tempList: [],
      checkedId: '',
      warnFlag: false,
      timer: {},
      options: '',
      checkedInfo: '',
      errorDesc: undefined,
      modal: false,
      modalType: 0
    }
  },
  mounted () {
    this.loadTableData()
    this.timer = setInterval(() => {
      this.loadTableData()
    }, this.time)
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    getInput (e) {
      console.log(e)
      this.errorDesc = e
    },
    getEng (id) {
      let engList = ['A', 'B', 'C', 'D', 'E', 'F']
      return engList[id - 1]
    },
    freshTimeChange (item) {
      clearInterval(this.timer)
      console.log(item)
      this.timer = setInterval(() => {
        this.loadTableData()
      }, item)
    },
    showModal (type) {
      this.modalType = type
      this.modal = true
    },
    changeState (type) {
      this.checkedInfo.errorDesc = this.errorDesc
      changeTemperatureState(this.checkedInfo).then(res => {
        this.errorDesc = undefined
        if (res.code === 200) {
          this.loadTableData()
        } else {
          this.$Message.warning(res.msg)
        }
      })
    },
    changeChecked (item) {
      this.checkedId = item.id
      this.checkedInfo = item || this.tempList[0]
    },
    loadTableData (clear) {
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      this.pageInfo.loading = true
      getTempLineTranslate().then(res => {
        if (res.code === 200) {
          let value = []
          if (res.data !== '') {
            let info = JSON.parse(res.data)
            for (let i in info.data) {
              let item = info.data[i]
              value.push({ x: i, temp: item })
            }
            this.initChart(value)
          }
        }
      })
      getTemperatureInfo().then(res => {
        if (res.code === 200) {
          this.tempList = res.data
          if (this.checkedId === '') {
            this.checkedId = res.data[0].id
            this.checkedInfo = res.data[0]
          }
        }
      })
    },
    initChart (value) {
      this.options = {
        title: '温度曲线',
        type: 'line',
        xAxis: {
          key: 'x'
        },
        yAxis: {
          symbol: '℃'
        },
        value: value,
        legend: [
          {
            key: 'temp',
            name: '温度',
            color: '#0084FF'
          }
        ]
      }
      this.$nextTick(() => {
        this.$refs.chart.init()
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../../../style/varient";
@size-padding: 10px;
/deep/.ivu-modal-footer{
  display: flex;
  justify-content: right;
  button{
     &:first-child{
       background-color: #212221;
       color: #eee;
     }
  }
}
.common-flex {
  display: flex;
  align-items: center;
}
.judge {
  margin-bottom: 30px;
}
.report-generate {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: calc(~"100% - " 50px);
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
  overflow: hidden;
}
.right-boxes {
  display: flex;
  flex-wrap: no-wrap;
  margin-top: 20px;
  // overflow: scroll;
  // max-height: 380px;
  background: #000201;
  border-radius: 4px;
  padding: 12px 12px 2px 12px;
  min-width: 90%;
  width: fit-content;
  .box {
    cursor: pointer;
    text-align: center;
    width: 190px;
    background: #000201;
    margin-right: 24px;
    // margin-bottom: 20px;
    // border: 1px solid #aaa;
    // height: 107px;
    // border: 1px solid #0084FF;
    &:nth-child(7n) {
      margin-right: 0 !important;
    }
    .nameline {
      padding: 6px 0 6px 12px;
      // border: 1px solid #000;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
    .value {
      font-size: 14px;
      font-family: Alibaba PuHuiTi;
      line-height: 32px;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 76px;
      background: #252d3a;
      margin-bottom: 10px !important;
    }
  }
}
.link {
  width: 100%;
  margin-top: 30px;
  .title {
    margin-bottom: 12px;
    // color: rgba(0, 0, 0, 0.8);
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    font-family: Alibaba PuHuiTi;
    font-weight: 400;
  }
  .chart-container {
    width: 100%;
    height: 320px;
    padding: 11px 14px;
    background: #19181d;
    border: 1px solid rgba(0, 0, 0, 0.050980392156862744);
    border-radius: 8px;
  }
}
</style>



