
import suanpan from '@/libs/api.suanpan'
import axios from '@/libs/api.request'

export const getTempLine = (params) => {
  return suanpan.request({
    url: '/8888/predicted',
    method: 'get'
  })
}

export const getTempLineTranslate = (params) => {
  return axios.request({
    url: '/third/temperature/curve',
    method: 'get'
  })
}



